@import "./listTable-utils";

// Grid
.list-table {
  width: 80%;
  margin: 0 auto;
  margin-top: 3rem;
  @include responsivePhones {
    width: 86%;
    margin-top: 2rem;
  }
}
.listTable-wrapper {
  display: grid;
  width: 100%;
  margin: 0 auto;
  height: fit-content;
  font-size: 0.8rem;
  margin-bottom: 1rem;
  grid-template-columns: 100%;
  grid-gap: 10px;
  row-gap: 15px;
}
